import React, { useState, useEffect } from 'react'
import axios from 'axios'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import './home.less'
import down from '../../assets/img/down.svg'
import Footer from '../../components/Footer/Footer'
import StatsBox from './StatsBox'
import VideoWebm from './video/DERI-Illustration-cut.webm'
import AKG from './img/AKG.svg'
import AutoFarm from './img/AutoFarm.svg'
import BASIS from './img/BASIS.svg'
import binance from './img/binance.svg'
import BIXIN from './img/BIXIN.svg'
import blackRange from './img/black-range.svg'
import CDF from './img/CDF.svg'
import dForce from './img/dForce.svg'
import Kryptos from './img/Kryptos.svg'
import FBG from './img/FBG.svg'
import GSR from './img/GSR.svg'
import LD from './img/LD.svg'
import LotusCapital from './img/LotusCapital.svg'
import WOO from './img/WOO.svg'
import QCP from './img/QCP.svg'
import Arbitragers from './img/Arbitragers.png'
import liquidityProviders from './img/liquidity-providers.png'
import PositionLiquidators from './img/Position-liquidators.png'
import stepone from './img/step1.png'
import steptwo from './img/step2.png'
import stepthree from './img/step3.png'
import trade from './img/trade.png'
export default function Home({ lang }) {
  const [data, setData] = useState({})
  const [isVolumeActive, setIsVolumeActive] = useState(true)
  const [isTransactionActive, setIsTransactionActive] = useState(true)
  const loadData = async () => {
    const url = `${process.env.REACT_APP_INFO_HTTP_URL}/stats_for_homepage`
    const res = await axios.get(url);
    if (res.status === 200) {
      setData(res.data)
    }
  }
  useEffect(() => {
    loadData();
    const interval = setInterval(() => {
      loadData();
      setIsTransactionActive(true)
      setIsVolumeActive(true)
      // setData({volume : 1277053675.20694 + 100,transactions :13025 + 200})
    }, 1000 * 60 * 5)
    return () => clearInterval(interval)
  }, [])
  return (
    <div className='home'>
      <div className='home-box'>
     
        <div className='first-screen'>
          <div className='video'>
            <video muted autoplay='autoplay' loop   >
              <source src={VideoWebm} type="video/webm" />
            </video>
          </div>

          <div className='title'>
            <div className='title-text'>
              {lang['title']}
            </div>
            <div className='deri-describe'>
              = ( {lang['perpetual-futures']} + {lang['everlasting-options']} ) {lang['deri-describe']}
            </div>
            <div className='button-home'>
              <a className='start-trading' href='https://app.deri.finance/'>{lang['start-trading']}</a>
              <a className='read-our' target='_blank' href=' https://docs.deri.finance/whitepaper'>{lang['read-our-whitepaper']}</a>
            </div>
          </div>
          {/* <div className='down'>
            <img alt='' src={down} />
          </div> */}
        </div>
        <div className='second-screen'>
          <div className='current-stats'>
            <div className='current-stats-title'>
              {lang['current-stats']}
            </div>
            {/* <div className='current-stats-box'>
              <div className='total-volume'>
                <div className='num'>$ <DeriNumberFormat value={data.volume} decimalScale={0} thousandSeparator={true} /></div>
                <div className='title-name'>{lang['total-volume']}</div>
              </div>
              <div className='total-transactions'>
                <div className='num'>
                  <DeriNumberFormat value={data.transactions} decimalScale={0} thousandSeparator={true} />
                </div>
                <div className='title-name'>
                  {lang['total-transactions']}
                </div>
              </div>
            </div> */}
            <StatsBox lang={lang} />
          </div>
          <div className='who-uses-deri'>
            <div className='who-uses-deri-title'>
              {lang['who-uses-deri']}
            </div>
            {/* <div className='who-uses-deri-describe'>
              {lang['who-uses-describe']}
            </div> */}
            <div className='who-uses-deri-box'>
              <div className='deri-role'>
                <div className='role-logo'>
                  <img alt='' src={liquidityProviders} />
                </div>
                <div className='role-title-describe'>
                  <div className='role-title'>
                    {lang['liquidity-providers']}
                  </div>
                  <div className='role-describe'>
                    {lang['liquidity-providers-describe']}
                  </div>
                </div>
              </div>
              <div className='deri-role'>
                <div className='role-logo'>
                  <img alt='' src={trade} />
                </div>
                <div className='role-title-describe'>
                  <div className='role-title'>
                    {lang['traders']}
                  </div>
                  <div className='role-describe'>
                    {lang['traders-describe']}
                  </div>
                </div>
              </div>
              <div className='deri-role'>
                <div className='role-logo'>
                  <img alt='' src={PositionLiquidators} />
                </div>
                <div className='role-title-describe'>
                  <div className='role-title'>
                    {lang['position-liquidators']}
                  </div>
                  <div className='role-describe'>
                    {lang['position-liquidators-describe']}
                  </div>
                </div>
              </div>
              <div className='deri-role'>
                <div className='role-logo'>
                  <img alt='' src={Arbitragers} />
                </div>
                <div className='role-title-describe'>
                  <div className='role-title'>
                    {lang['arbitragers']}
                  </div>
                  <div className='role-describe'>
                    {lang['arbitragers-describe']}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className='how-to-start-trading'>
            <div className='how-to-start-trading-title'>
              {lang['how-to-start-trading']}
            </div>
            <div className='step-connect-wallet'>
              <div className='step-logo logo-one'>
                <img alt='' src={stepone} />
              </div>
              <div className='step-box'>
                <div className='step-title'>
                  {lang['step-connect-wallet']}
                </div>
                <div className='step-describe'>
                  {lang['step-connect-wallet-describe']}
                </div>
              </div>
            </div>
            <div className='step-hr-one'>
              <div className='hr-one'></div>
              <div className='hr-tow'></div>
              <div className='hr-three'></div>
            </div>
            <div className='step-deposit-margin'>
              <div className='step-logo logo-two'>
                <img alt='' src={steptwo} />
              </div>
              <div className='step-box'>
                <div className='step-title'>
                  {lang['step-deposit-margin']}
                </div>
                <div className='step-describe'>
                  {lang['step-deposit-margin-describe']}
                </div>
              </div>
            </div>
            <div className='step-hr-two'>
              <div className='hr-one'></div>
              <div className='hr-tow'></div>
            </div>
            <div className='step-trade-derivatives'>
              <div className='step-logo logo-three'>
                <img alt='' src={stepthree} />
              </div>
              <div className='step-box'>
                <div className='step-title'>
                  {lang['step-trade-derivatives']}
                </div>
                <div className='step-describe'>
                  {lang['step-trade-derivatives-describe']}
                </div>
              </div>
            </div>
          </div>
          <div className='backed-by'>
            <div className='backed-by-title'>
              {lang['backed-by']}
            </div>
            <div className='backed-by-box'>
              <div>
                <img alt='' src={FBG} />
                <img alt='' src={BIXIN} />
                <img alt='' src={LotusCapital} />
                <img alt='' src={blackRange} />
                <img alt='' src={LD} />
                <img alt='' src={Kryptos} />
              </div>
              <div className='padding-box'>
                <img alt='' src={AKG} />
                <img alt='' src={CDF} />
                <img alt='' src={GSR} />
                <img alt='' src={WOO} />
                <img alt='' src={QCP} />
                <img alt='' src={binance} />
              </div>
            </div>
          </div>
          <div className='our-partners'>
            {/* <div className='our-partners-title'>
              {lang['our-partners']}
            </div> */}
            <div className='our-partners-box'>
              {/* <div>
                <img alt='' src={AutoFarm} />
                <img alt='' src={BASIS} />
                <img alt='' src={dForce} />
              </div> */}
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  )
}